

.text-title-cafet {
    margin-top: 200px;
    margin-bottom: 150px;
    color: #1A886E;
    border-radius: 10px;
    font-family: 'Gill Sans', sans-serif;
    border-bottom: solid #1A886E;
    border-top: solid #1A886E;
    border-left: solid #1A886E;
    border-right: solid #1A886E;
    padding: 30px;
    max-width: 25%;
    text-align: center;
    margin: auto;
    text-align: center;
    margin-top: 100px;
}

.balise-p-cafet {
    margin: auto;
    margin-top: 80px;
    margin-bottom: 60px;
    max-width: 60%;
    font-size: 25px;
    font-family: 'Gill Sans', sans-serif;
    text-align: justify;
}

.cafet-titre {
    margin-top: 10px;
    border-bottom:solid #1A886E;
    color: #1A886E;
    max-width: 50%;
    margin:auto;
    font-size: 30px;
    padding:10px;
}

.cafet-paragraph {
    text-align: center;
    font-family: 'Gill Sans', sans-serif;
    border-radius: 15px;
    margin-top: 50px;
    margin-bottom: 100px;
    padding: 35px;
    
}

.page-container {
    background: linear-gradient( rgb(250, 245, 227),rgb(255, 255, 255));
    margin-top: -20px;
}

.content-container-cafet {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    background-color: #FFF;
    width: 80%;
    margin: auto;
    border-radius: 25px;
    margin-bottom: 150px;
    height: flex;
    margin-top: 120px;
}

.cafet-img-container img{
    text-align: center;
    padding: 40px;
    margin: 0 10px;
    flex: 1 1 auto;
    max-width: 100%;
    height: auto;
}

.paragraph-cafet {
    text-align: center;
}

.tampoon {
    color: transparent;
}

@media (max-width: 500px)  {
    .balise-p-cafet {
    max-width: 100%;
    font-size: 16px;
    }
}

@media (max-width: 500px)  {
    .text-title-cafet {
    max-width: 100%;
    font-size: 20px;
    }
    .cafet-img-container img {
        padding: 10px;
        max-width: 95%;
    }
}