.text-title-naturo {
    color: #1A886E;
    border-radius: 10px;
    font-family: 'Gill Sans', sans-serif;
    border-bottom: solid #1A886E;
    border-top: solid #1A886E;
    border-left: solid #1A886E;
    border-right: solid #1A886E;
    padding: 30px;
    max-width: 25%;
    text-align: center;
    margin: auto;
    text-align: center;
}

.balise-p-naturo{
    margin: auto;
    margin-top: 80px;
    margin-bottom: 60px;
    max-width: 60%;
    font-size: 25px;
    font-family: 'Gill Sans', sans-serif;
    text-align: justify;
}

.title-paragraph-naturopathe {
    margin-top: 10px;
    margin-bottom: 10px;
    border-bottom:solid #1A886E;
    color: #1A886E;
    max-width: 50%;
    margin:auto;
    font-size: 25px;
    padding:20px;
    text-align: center;
    font-family: 'Gill Sans', sans-serif;
}

.title-naturo-part {
    color: #1A886E;
    max-width: 50%;
    margin:auto;
    font-size: 20px;
    padding:40px;
    text-align: center;
    font-family: 'Gill Sans', sans-serif;
}


.naturo-paragraph {
    text-align: center;
    font-family: 'Gill Sans', sans-serif;
    border-radius: 15px;
    margin-top: 50px;
    margin-bottom: 100px;
    padding: 35px;
    
}

.naturo-page-container {
    background: linear-gradient( rgb(250, 245, 227),rgb(255, 255, 255));
}

.content-container-naturo {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    background-color: #FFF;
    width: 80%;
    margin: auto;
    border-radius: 25px;
    margin-bottom: 150px;
    height: flex;
    margin-top: 120px;
}

.naturo-img-container img{
    text-align: center;
    padding: 70px;
    margin: 0 10px;
    flex: 1 1 auto;
    max-width: 100%;
    height: auto;
}

.naturo-img-container {
    margin: auto;
    text-align: center;

    padding: Opx;
    max-width: 100%;
}

.paragraph-cafet {
    text-align: center;
}

.tampoon-naturo {
    color: transparent;
    background-color: rgb(250, 245, 227);
    height: 200px;
}

@media (max-width: 700px) {
    .balise-p-naturo {
        font-size: 18px;
    }
    .text-title-naturo {
        max-width: 100%;
        font-size: 27px;
        padding: 16px;
    }
    .title-naturo-part {
        max-width: 100%;
        margin-top: 40px;
        margin-bottom: -40px;
        font-size: 18px;
    }
    .title-paragraph-naturopathe {
        max-width: 80%;
        font-size: 18px;
    }
}