.container-slider {
  max-width: 1920px;
  height: 680px;
  margin: 100px auto 0;
  position: relative;
  overflow: hidden;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  margin-top: 110px;
}
@media screen and (max-width: 700px){
  /* .container-slider {
    margin: 100px 10px 0;
  } */
}
.slide {
  width: 100%;
  height: 100%;
  position: absolute;
  opacity: 0;
  transition: opacity ease-in-out 0.4s;
}
.slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  margin: 0 10px;
  margin: auto;
  flex: 1 1 auto;
}
.active-anim {
  opacity: 1;
}

.btn-slide {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background:#f1f1f1;
  border: 1px solid rgba(34, 34, 34, 0.287);
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.btn-slide img {
  width: 25px;
  height: 25px;
  pointer-events: none;
}
.prev {
  top: 50%;
  left: 20px;
  transform: translateY(-60%);
}
.next {
  top: 50%;
  right: 20px;
  transform: translateY(-60%);
}


.container-dots {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
}
.dot {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 3px solid #f1f1f1;
  margin: 0 5px;
  background: #f1f1f1;
}
.dot.active {
  background: rgba(255, 176, 29, 0.808);
}

@media (max-width:700px) {
  .container-slider {
    height: 300px;
  }
  .container-slider img{
    margin: auto;
  }
  .btn-slide:hover {
    color: ease-out 0,2s #f1f1f1;
  }
  .btn-slide {
    background: rgba(189, 187, 187, 0.336);
  }
  .dot {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 2px solid #f1f1f1;
    margin: 0 5px;
    background: #f1f1f179;
  }
  .dot.active {
    background: rgba(255, 176, 29, 0.808);
  }
}