.homepage-container {
    background: linear-gradient(rgb(250, 245, 227),rgb(255, 255, 255));
}

.labelContainer {
    display: inline-flex;
    margin-left: flex;
    font-size: 17px;
    color: rgb(92, 91, 91);
    width: 100%;
    margin-top: 20px;
    margin-bottom: 80px;
}

.home-p-paragraph {
    margin: auto;
    margin-top: 80px;
    margin-bottom: 60px;
    max-width: 60%;
    font-size: 25px;
    font-family: 'Gill Sans', sans-serif;
    text-align: justify;
}

.home-paragraph-container {
    text-align: center;
    font-family: 'Gill Sans', sans-serif;
    border-radius: 15px;
    margin-top: 50px;
    margin-bottom: 100px;
    padding: 35px;
    
}

.home-pub-container {
    text-align: center;
    margin: auto;
    margin-top: 80px;
    margin-bottom: -40px;
}

.home-pub-container img {
    border-radius: 10px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    max-width: 100%;
}

.plantlogo {
    text-align: center;
    width: 33%;
    margin: auto;
}

.biologo {
    text-align: center;
    width: 33%;
    margin: auto;
}

.earthlogo {
    text-align: center;
    width: 33%;
    margin: auto;
}

.plantlogo img{
    width: 130px;
    height: auto;
    margin: auto;
}

.biologo img{
    width: 130px;
    height: auto;
    margin: auto;
}

.earthlogo img{
    width: 130px;
    height: auto;
    margin: auto;
}

.magasin-image {
    margin-top: 40px;
    color: #1f866e;
    border-bottom: solid #1f866e;
    max-width: 30%;
    margin:auto;
    text-align: center;
    font-size: 30px;
    padding:10px;
}

@media (max-width:700px) {
    .homepage-container {
        margin-top: -400px;
    }
    .labelContainer {
        font-size: 15px;
        display: inline-block;
        margin-top: 150px;
    }
    .earthlogo img{
        width: 90px;
    }
    .biologo img{
        width: 90px;
    }
    .plantlogo img{
        width: 90px;
    }

    .plantlogo {
        margin-top: 30px;
        width: 100%;
        
    }
    .earthlogo {
        margin-top: 30px;
        width: 100%;
    }
    .biologo {
        margin-top: 30px;
        width: 100%;
    }
    .magasin-image {
        font-size: 20px;
        max-width: 80%;
    }
    .home-p-paragraph {
        max-width: 100%;
        font-size: 20px;
    }
    .home-pub-container {
        display: inline-block;
        max-width: 100%;
        padding: 0px;
    }
}