.text-title-soincorps {
    color: #1A886E;
    border-radius: 10px;
    font-family: 'Gill Sans', sans-serif;
    border-bottom: solid #1A886E;
    border-top: solid #1A886E;
    border-left: solid #1A886E;
    border-right: solid #1A886E;
    padding: 30px;
    max-width: 25%;
    text-align: center;
    margin: auto;
    text-align: center;
}

.balise-p-soincorps {
    margin: auto;
    margin-top: 40px;
    max-width: 60%;
    font-size: 20px;
    font-family: 'Gill Sans', sans-serif;
    text-align: justify;
}

.soincorps-paragraph {
    text-align: center;
    font-family: 'Gill Sans', sans-serif;
    border-radius: 15px;
    margin-top: 50px;
    margin-bottom: 100px;
    padding: 35px;
    
}

.soincorps-img-container img{
    text-align: center;
    padding: 30px;
    border-radius: 35px;
    text-align: left;
    margin: 0 10px;
    flex: 1 1 auto;
    max-width: 100%;
    height: auto;
    margin-bottom: 80px;
    margin-top: 50px;
}

.soincorps-page-container {
    background: linear-gradient( rgb(250, 245, 227),rgb(255, 255, 255));
}

.content-container-soincorps {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    background-color: #FFF;
    width: 80%;
    margin: auto;
    border-radius: 25px;
    margin-bottom: 150px;
    height: flex;
    margin-top: 120px;
}

.soincorps-img-container img{
    text-align: center;
    padding: 70px;
    margin: 0 10px;
    flex: 1 1 auto;
    max-width: 100%;
    height: auto;
}

.balise-p-soincorps{
    margin: auto;
    margin-top: 80px;
    margin-bottom: 60px;
    max-width: 60%;
    font-size: 25px;
    font-family: 'Gill Sans', sans-serif;
    text-align: justify;
}

.soincorps-titre {
    margin-top: 10px;
    border-bottom:solid #1A886E;
    color: #1A886E;
    max-width: 40%;
    margin:auto;
    font-size: 25px;
    padding:10px;
    text-align: center;
    font-family: 'Gill Sans', sans-serif;
}

.soincorps-paragraph {
    text-align: center;
    font-family: 'Gill Sans', sans-serif;
    border-radius: 15px;
    margin-top: 50px;
    margin-bottom: 100px;
    padding: 35px;
    
}

.paragraph-soincorps {
    text-align: center;
}

.tampoon-soincorps {
    color: transparent;
    background-color: rgb(250, 245, 227);
}

@media (max-width: 700px) {
    .soincorps-titre {
        padding: 15px;
        max-width: 80%;
        font-size: 16px;
    }
    .balise-p-soincorps{
        font-size: 18px;
    }
    .text-title-soincorps {
        max-width: 100%;
        font-size: 25px;
    }
    .soincorps-img-container img{
        max-width: 360px;
        padding: 10px;
        border-radius: 0px;
    }
}