.mae-contact {
    margin-top: 50px;
    margin: auto;
    text-align: center;
}

.mae-contact-title {
    font-family: 'Gill Sans', sans-serif;
    font-size: 22px;
    max-width: 33%;
    margin: auto;
    margin-bottom: 70px;
    color: rgb(134, 134, 134);
    text-align: center;
}

.mae-facebook {
    font-size: 87px;
    color: rgb(98, 146, 235);
    display: inline-block;
    margin-left: 100px;
    margin-bottom: 80px;
}

.mae-instagram {
    font-size: 85px;
    height: 87px;
    width: 85px;
    display: inline-block;
    border-radius: 29px;
    color: rgb(255, 255, 255);
    background: #d6249f;
    background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%,#d6249f 60%,#285AEB 90%);
    margin-bottom: 80px;
}

.mae-contact-icon {
    display: inline-block;
}

@media (max-width: 500px) {
    .mae-contact {
        margin-bottom: -70px;
    }
    .mae-contact-title {
        max-width: 90%;
        font-size: 19px;
    }
    .mae-facebook {
        font-size: 70px;
        margin-left: 50px;
        margin-bottom: 140px;
    }
    .mae-instagram {
        font-size: 70px;
        height: 70px;
        width: 75px;
        border-radius: 25px;
        margin-bottom: 140px;
    }
}