.title-vrac {
    margin-top: 200px;
    margin-bottom: 150px;
    color: #1A886E;
    border-radius: 10px;
    font-family: 'Gill Sans', sans-serif;
    border-bottom: solid #1A886E;
    border-top: solid #1A886E;
    border-left: solid #1A886E;
    border-right: solid #1A886E;
    padding: 20px;
    max-width: 25%;
    text-align: center;
    margin: auto;
}

.balise-p-vrac {
    margin: auto;
    margin-top: 80px;
    margin-bottom: 60px;
    max-width: 60%;
    font-size: 25px;
    font-family: 'Gill Sans', sans-serif;
    text-align: justify;
}

.vrac-titre {
    margin-top: 10px;
    border-bottom:solid #1A886E;
    color: #1A886E;
    max-width: 40%;
    margin:auto;
    font-size: 30px;
    padding:10px;
}

.vrac-paragraph {
    text-align: center;
    font-family: 'Gill Sans', sans-serif;
    border-radius: 15px;
    margin-top: 50px;
    margin-bottom: 100px;
    padding: 35px;
    
}
.vrac-img-container img{
    text-align: center;
    padding: 70px;
    margin: 0 10px;
    flex: 1 1 auto;
    max-width: 100%;
    height: auto;
}
.vrac-img-container {
    margin: auto;
}

.page-container-vrac {
    background: linear-gradient( rgb(250, 245, 227),rgb(255, 255, 255)) ;
}

.tampoon {
    color: transparent;
}

.content-container-vrac {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    background-color: #FFF;
    width: 80%;
    margin: auto;
    border-radius: 25px;
    margin-bottom: 150px;
    height: flex;
}

@media only screen and (max-width: 700px) {
    .vrac-img-container  {
    width: 49.99999%;
    margin: 6px 0;
    }
}

@media only screen and (max-width: 500px) {
    .vrac-img-container  {
    width: 100%;
    }
}

@media (max-width: 500px)  {
    .balise-p-vrac {
    max-width: 100%;
    font-size: 16px;
    }
    .title-vrac {
        font-size: 18px;
        max-width: 100%;
        }
    .vrac-titre {
        font-size: 21px;
        max-width: 100%;
    }
}