.text-title-livraisons {
    color: #1A886E;
    border-radius: 10px;
    font-family: 'Gill Sans', sans-serif;
    border-bottom: solid #1A886E;
    border-top: solid #1A886E;
    border-left: solid #1A886E;
    border-right: solid #1A886E;
    padding: 30px;
    max-width: 25%;
    text-align: center;
    margin: auto;
    text-align: center;
}

.balise-p-livraisons {
    margin: auto;
    margin-top: 80px;
    margin-bottom: 60px;
    max-width: 50%;
    font-size: 25px;
    padding: 10px;
    font-family: 'Gill Sans', sans-serif;
    text-align: center;
}

.livraisons-paragraph {
    text-align: center;
    font-family: 'Gill Sans', sans-serif;
    border-radius: 15px;
    margin-top: 50px;
    margin-bottom: 100px;
    padding: 35px;
    
}

.livraisons-img-container img{
    text-align: center;
    padding: 30px;
    border-radius: 35px;
    text-align: left;
    margin: 0 10px;
    flex: 1 1 auto;
    max-width: 100%;
    height: auto;
    margin-bottom: 80px;
    margin-top: 50px;
}

.livraisons-page-container {
    background: linear-gradient( rgb(250, 245, 227),rgb(255, 255, 255));
}

.content-container-livraisons {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    background-color: #FFF;
    width: 80%;
    margin: auto;
    border-radius: 25px;
    margin-bottom: 150px;
    height: flex;
    margin-top: 120px;
}

.livraisons-img-container img{
    text-align: center;
    padding: 70px;
    margin: 0 10px;
    flex: 1 1 auto;
    max-width: 100%;
    height: auto;
}

