.text-title-contact {
    margin: auto;
    margin-top: 120px;
    margin-bottom: 100px;
    color: #1A886E;
    border-radius: 10px;
    font-family: 'Gill Sans', sans-serif;
    border-bottom: solid #1A886E;
    border-top: solid #1A886E;
    border-left: solid #1A886E;
    border-right: solid #1A886E;
    padding: 30px;
    max-width: 25%;
    text-align: center;
}

.contact-img-container img{
    text-align: center;
    margin: 0 120px;
    flex: 1 1 auto;
    max-width: 80%;
    height: auto;
    border-radius: 20px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.contact-img-container {
    margin: auto;
    padding: 10px;
}


.contact-list{
    text-align: center;
    max-width: 70%;
    margin: auto;
    margin-bottom: 120px;
    margin-top: 80px;
    font-family: 'Gill Sans', sans-serif;
}

.icon-svg {
    font-size: 20px;
    margin-left: auto;
    padding: 10px;
    max-width: 70%;
    display: block;
    text-align: left;
}

.contact-container {
    margin: auto;
    text-align: center;

}
.contact-subcontainer {
    display: inline-table;
    text-align: center;
    padding: 50px;
    margin: auto;
    max-width: 100%;
}

.icon-phone {
    color: #20ad27;
    font-size: 35px;
}
.icon-mail {
    color: #2075ad;
    font-size: 35px;
}
.icon-ping {
    color: #c9004d;
    font-size: 35px;
}

.page-container-contact {
    background: linear-gradient( rgb(250, 245, 227),rgb(255, 255, 255)) ;
}

.tampoon-contact {
    color: transparent;
    background: rgb(250, 245, 227);
}

.content-container-contact {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    background-color: #FFF;
    width: 80%;
    margin: auto;
    border-radius: 25px;
    margin-bottom: 150px;
    height: flex;
    padding: 50px;
    margin: auto;
}

.content-container-contact {
    margin: auto;
}


@media (max-width: 1200px) {
    .contact-img-container img {
        max-width: 100%;
        margin: auto;
    }
}

@media (max-width: 700px) {
    .contact-subcontainer {
        font-size: 15px;
        max-width: 100%;
        padding: 20px;
        display: inline-flex;
    }
    .contact-list {
        max-width: 100%;
        padding: 2px;
    }
    .contact-img-container {
        width: auto;
    }
    .contact-img-container img {
        max-width: 100%;
        margin: auto;
    }
    .text-title-contact {
        max-width: 100%;
        font-size: 19px;
    }
}