.text-title-epicerie {
    color: #1A886E;
    border-radius: 10px;
    font-family: 'Gill Sans', sans-serif;
    border-bottom: solid #1A886E;
    border-top: solid #1A886E;
    border-left: solid #1A886E;
    border-right: solid #1A886E;
    padding: 30px;
    max-width: 30%;
    text-align: center;
    margin: auto;
    text-align: center;
}

.balise-p-epicerie {
    margin: auto;
    margin-top: 80px;
    margin-bottom: 60px;
    max-width: 60%;
    font-size: 25px;
    font-family: 'Gill Sans', sans-serif;
    text-align: justify;
}

.titre-paragraphe-epicerie {
    margin-top: 10px;
    border-bottom:solid #1A886E;
    text-align: center;
    color: #1A886E;
    max-width: 53%;
    margin:auto;
    font-size: 28px;
    padding:10px;
}


.epicerie-img-container img{
    text-align: center;
    padding: 10px;
    margin: 0 10px;
    flex: 1 1 auto;
    max-width: 100%;
    height: auto;
    margin-bottom: 30px;
    margin-top: 30px;
}

.link-button-ep {
    background-color: #2ea35bc4;
    color: #FFF;
    max-width: 20%;
    padding: 20px;
    text-align: center;
    margin: auto;
    border-radius: 30px;
}
.link-button-ep:hover {
}

.epicerie-img-container {
    margin: auto;
    text-align: center;
}

.epicerie-page-container {
    background: linear-gradient( rgb(250, 245, 227),rgb(255, 255, 255));
}

.content-container-epicerie {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    background-color: #FFF;
    width: 80%;
    margin: auto;
    border-radius: 25px;
    margin-bottom: 150px;
    height: flex;
    margin-top: 120px;
}

.epicerie-img-container img{
    text-align: center;
    padding: 70px;
    margin: 0 10px;
    flex: 1 1 auto;
    max-width: 100%;
    height: auto;
}

.paragraph-epicerie {
    text-align: center;
}

.tampoon-epicerie {
    color: transparent;
    background-color: rgb(250, 245, 227);
}

@media (max-width: 700px) {
    .balise-p-epicerie {
        font-size: 18px;
    }

    .text-title-epicerie {
        max-width: 100%;
        font-size: 25px;
    }
    .titre-paragraphe-epicerie {
        max-width: 80%;
        font-size: 20px;

    }
    .link-button-ep {
        height: 80px;
        max-width: 80%;
        font-size: 17px;
    }
    .epicerie-img-container img {
        padding: 10px;
        margin: auto;
        border-radius: 20px;
    }
    .epicerie-img-container {
        margin-top: 40px;
    }
}