.page-container {
    background: linear-gradient( rgb(250, 245, 227),rgb(255, 255, 255));
}

.tampoon {
    color: transparent;
}

.content-container-mae {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    background-color: #FFF;
    width: 80%;
    margin: auto;
    border-radius: 25px;
    margin-bottom: 120px;
    height: flex;
    margin-top: 120px;
}

.balise-p-mae {
  margin: auto;
  margin-top: 40px;
  max-width: 60%;
  font-size: 25px;
  font-family: 'Gill Sans', sans-serif;
}

.mae-paragraph-container{
  text-align: center, justify;
  border-radius: 15px;
  margin-top: 70px;
  margin-bottom: 50px;
  padding: 30px;
  }

.paragraph-mae {
  margin-top: 100px;
  margin-bottom: 30px;
}

.mae-img-container img{
    text-align: center;
    padding: 70px;
    margin: 0 10px;
    flex: 1 1 auto;
    max-width: 100%;
    height: auto;
}

.menu-mae img{
  text-align: center;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  padding: 30px;
  border-radius: 20px;
  text-align: left;
  margin: 0 10px;
  flex: 1 1 auto;
  max-width: 100%;
  height: auto;
  margin-bottom: 80px;
  margin-top: 50px;
}

.menu-mae {
  text-align: center;
  padding: 0px;
  margin-top: 50px;
  margin-bottom: 50px;
  flex: 1 1 auto;
  max-width: 100%;
  height: auto;
}

.mae-title-paragraph {
  
  margin-bottom: 100px;
  margin-top: 30px;
  font-size: 30px;
  font-family: 'Gill Sans', sans-serif;
  color:#1A886E;
  border-bottom:solid #1A886E;
  text-align: center;
  margin: auto;
  max-width: 38%;
  padding: 10px;
}

.title-mae {
    margin-top: 200px;
    margin-bottom: 150px;
    color: #1A886E;
    border-radius: 10px;
    font-family: 'Gill Sans', sans-serif;
    border-bottom: solid #1A886E;
    border-top: solid #1A886E;
    border-left: solid #1A886E;
    border-right: solid #1A886E;
    padding: 30px;
    max-width: 25%;
    text-align: center;
    margin: auto; 
    margin-top: 120px;
    margin-bottom: 120px;
}

.text-mae {
    margin-top: 60px;
    text-align: center;
    padding: 40px;
}

  @media only screen and (max-width: 700px) {
    .fruitslegumes-img-container  {
      width: 49.99999%;
      margin: 6px 0;
    }
  }
  
  @media only screen and (max-width: 500px) {
    .mae-img-container  {
      width: 100%;
    }
  }

  @media (max-width: 500px)  {
    .balise-p-mae {
      max-width: 100%;
      font-size: 16px;
    }
    .menu-mae img {
      max-width: 90%;
      margin-bottom: 25px;
      margin-top: -40px;
    }
    .title-mae {
      max-width: 100%;
      font-size: 20px;
    }
    .mae-title-paragraph {
      max-width: 80%;
      font-size: 19px;
    }
    .menu-mae img {
      margin-top: 30px;
    }
    .mae-img-container img{
      padding: 10px;
      width: 95%;
    }
  }